  background: -webkit-gradient(linear, left top, left bottom, color-stop(70%, rgba(255, 255, 255, 0)), color-stop(30%, #bfdffd));
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 70%, #bfdffd 30%);
}

.highlight-green {
  background: -webkit-gradient(linear, left top, left bottom, color-stop(70%, rgba(255, 255, 255, 0)), color-stop(30%, #d0ffb5));
