/* photo grid */
.grid-container {
  margin: 0 auto;
  padding: 0 1rem;
  max-width: 1200px;
}

.responsive-img {
  max-width: 100%;
  height: auto;
}

.cell img {
  display: block;
  object-fit: cover;
}

.cell {
  /* margin: 1rem; */
  /* padding: 2rem; */
  /* border: 1px solid #444444; */
}

@media screen and (min-width: 400px) {
  .grid {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
  }

  .cell-2x2>.cell {
    width: calc(50% - 2rem);
    min-width: 153px;
  }

  .cell {
    width: calc(50% - 2rem);
    max-width: 100%;
    min-width: 150px;
    height: 2rem;
    text-align: center;
  }

  .cell-3x3>.cell {
    width: calc(30% - 2rem);
    min-width: 96px;
  }

  .cell-2x6>.cell {
    width: calc(20% - 2rem);
  }

  .cell-1x1>.cell {
    width: 100%;
    min-width: 340px;
    max-width: 600px;
  }
}

.narrow-cell {
  max-width: 50%;
  max-height: 50%;
}

/* site title */
.site-header {
  background-color: #ffffff;
  grid-column: 1 / 3;
  position: static;
  top: 10%;
