/* landing page & image */
@media screen and (max-width: 748px) {
  .headline-grid {
    padding: 20px;
    margin-bottom: 30px;
  }

  .head-img img {
    max-width: 70%;
    max-height: 50%;
    vertical-align: center;
  }

  .head-img,
  .first-line {
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    line-height: normal;
  }

  .head-text {
    text-align: justify;
    padding: 30px;
  }
}

@media screen and (min-width: 749px) {

  /* 749px is when box shadow of pic doesn't look weird */
  .headline-grid {
    max-width: 1230px;
    position: relative;
    justify-content: flex-start;
    flex: auto;
    flex-direction: row;
    flex-wrap: wrap;
    margin-right: auto;
    margin-left: auto;
    padding: 1.6rem;
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 30px;
  }

  .head-wrap {
    line-height: 40px;
  }

  .head-text {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    line-height: 3.33333rem;
    font-weight: 500;
    font-style: normal;
  }

  .head-img {
    box-shadow: 5px 5px 5px lightgray;
    position: relative;
    max-width: 100%;
    max-height: 100%;
    height: auto;
    margin: 0 auto;
    margin-bottom: 100px;
    left: 0;
  }

  .head-img:hover {
    box-shadow: 20px 20px 5px lightgray;
    -webkit-transition: all 10 s ease-in-out;
    transition: all 10 s ease-in-out;
    -webkit-transition: all 10 s ease;
  }
}

/* PANEL GRID */
.projects {
  max-width: 1200px;
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  flex-flow: row wrap;
  align-content: flex-end;
  position: relative;
}

.panel {
  flex: 1 1;
  flex-basis: 45%;
  min-width: 300px;
  height: 420px;
  width: 300px;
  max-width: 25%;
  position: relative;
  border: 1px solid #efefef;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  margin-bottom: 20px;
  box-shadow: 2px 6px 25px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.panel-image {
  height: 140px;
  width: 94%;
  border: 5px double #8a8a8a;
  position: absolute;
  bottom: 0px;
  background-size: cover;
  background-position: 55% 45%;
  background-repeat: no-repeat;
  margin: 10px;
  background-color: #fabaca;
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}

.panel-image:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.panel:hover {
  box-shadow: 5px 9px 10px rgba(0, 0, 0, 0.2);
}

.panel h1 {
  text-align: center;
  width: 100%;
}

.subtitle {
  margin: 0 0 15px;
  font-weight: 700;
  text-align: center;
}

